import {
	ColorUtils,
	ExtensionProperty,
	IProperty,
	Nullable,
	PropertyType,
	Texture,
	TextureChannel,
	TextureInfo,
	vec3,
} from '@gltf-transform/core';
import { KHR_MATERIALS_TRANSLUCENCY } from '../constants';

interface ITranslucency extends IProperty {
	translucencyFactor: number;
	translucencyTexture: Texture;
	translucencyTextureInfo: TextureInfo;
	translucencyColorFactor: vec3;
	translucencyColorTexture: Texture;
	translucencyColorTextureInfo: TextureInfo;
}

const { R, G, B, A } = TextureChannel;

/**
 * # translucency
 *
 * Defines translucency reflectivity on a PBR {@link Material}. See {@link Materialstranslucency}.
 */
export class Translucency extends ExtensionProperty<ITranslucency> {
	public static EXTENSION_NAME = KHR_MATERIALS_TRANSLUCENCY;
	public declare extensionName: typeof KHR_MATERIALS_TRANSLUCENCY;
	public declare propertyType: 'Translucency';
	public declare parentTypes: [PropertyType.MATERIAL];

	protected init(): void {
		this.extensionName = KHR_MATERIALS_TRANSLUCENCY;
		this.propertyType = 'Translucency';
		this.parentTypes = [PropertyType.MATERIAL];
	}

	protected getDefaults(): Nullable<ITranslucency> {
		return Object.assign(super.getDefaults() as IProperty, {
			translucencyFactor: 1.0,
			translucencyTexture: null,
			translucencyTextureInfo: new TextureInfo(this.graph, 'translucencyTextureInfo'),
			translucencyColorFactor: [1.0, 1.0, 1.0] as vec3,
			translucencyColorTexture: null,
			translucencyColorTextureInfo: new TextureInfo(this.graph, 'translucencyColorTextureInfo'),
		});
	}

	/**********************************************************************************************
	 * translucency.
	 */

	/** translucency; linear multiplier. See {@link getTranslucencyTexture}. */
	public getTranslucencyFactor(): number {
		return this.get('translucencyFactor');
	}

	/** translucency; linear multiplier. See {@link getTranslucencyTexture}. */
	public setTranslucencyFactor(factor: number): this {
		return this.set('translucencyFactor', factor);
	}

	/** translucency color; Linear-sRGB components. See {@link getTranslucencyTexture}. */
	public getTranslucencyColorFactor(): vec3 {
		return this.get('translucencyColorFactor');
	}

	/** translucency color; Linear-sRGB components. See {@link getTranslucencyTexture}. */
	public setTranslucencyColorFactor(factor: vec3): this {
		return this.set('translucencyColorFactor', factor);
	}

	/** translucency color; sRGB hexadecimal color. See {@link getTranslucencyTexture} */
	public getTranslucencyColorHex(): number {
		return ColorUtils.factorToHex(this.getTranslucencyColorFactor());
	}

	/** translucency color; sRGB hexadecimal color. See {@link getTranslucencyTexture} */
	public setTranslucencyColorHex(hex: number): this {
		const factor = this.getTranslucencyColorFactor().slice() as vec3;
		return this.set('translucencyColorFactor', ColorUtils.hexToFactor(hex, factor));
	}

	/**
	 * translucency texture; linear multiplier. Configures the strength of the translucency reflection in
	 * the dielectric BRDF. A value of zero disables the translucency reflection, resulting in a pure
	 * diffuse material.
	 *
	 * Only the alpha (A) channel is used for translucency strength, but this texture may optionally
	 * be packed with translucency color (RGB) into a single texture.
	 */
	public getTranslucencyTexture(): Texture | null {
		return this.getRef('translucencyTexture');
	}

	/**
	 * Settings affecting the material's use of its translucency texture. If no texture is attached,
	 * {@link TextureInfo} is `null`.
	 */
	public getTranslucencyTextureInfo(): TextureInfo | null {
		return this.getRef('translucencyTexture') ? this.getRef('translucencyTextureInfo') : null;
	}

	/** Sets translucency texture. See {@link getTranslucencyTexture}. */
	public setTranslucencyTexture(texture: Texture | null): this {
		return this.setRef('translucencyTexture', texture, { channels: A });
	}

	/**
	 * translucency color texture; linear multiplier. Defines the F0 color of the translucency reflection
	 * (RGB channels, encoded in sRGB) in the the dielectric BRDF.
	 *
	 * Only RGB channels are used here, but this texture may optionally be packed with a translucency
	 * factor (A) into a single texture.
	 */
	public getTranslucencyColorTexture(): Texture | null {
		return this.getRef('translucencyColorTexture');
	}

	/**
	 * Settings affecting the material's use of its translucency color texture. If no texture is
	 * attached, {@link TextureInfo} is `null`.
	 */
	public getTranslucencyColorTextureInfo(): TextureInfo | null {
		return this.getRef('translucencyColorTexture') ? this.getRef('translucencyColorTextureInfo') : null;
	}

	/** Sets translucency color texture. See {@link getTranslucencyColorTexture}. */
	public setTranslucencyColorTexture(texture: Texture | null): this {
		return this.setRef('translucencyColorTexture', texture, { channels: R | G | B });
	}
}
